<template>
  <div ref="chartContainer" style="width: 100%; height: 400px;"></div>
</template>

<script>
import * as echarts from 'echarts'

export default {
  name: 'CoffeeSalesChart',
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      const chart = echarts.init(this.$refs.chartContainer)
      
      const options = {
        title: {
          text: '一周咖啡销量'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['咖啡销量']
        },
        xAxis: {
          type: 'category',
          data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
        },
        yAxis: {
          type: 'value'
        },
        series: [{
          name: '咖啡销量',
          type: 'bar',
          data: [120, 200, 150, 80, 70, 110, 130],  // 这代表每天的咖啡销量
          itemStyle: {
            color: '#4CAF50'  // 设置柱状图颜色
          }
        }]
      }
      
      chart.setOption(options)
    }
  }
}
</script>

